body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Fix Font Weight being too big */
h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a.active {
  color: rgb(5, 87, 194);
}

* {
    margin: 0px;
}

.App-header {
    background-color: rgb(32, 32, 32);
    color: rgb(224, 224, 224);
}

.App-header > h1 {
    padding: 0.5%;
}

div.Content {
    padding-top: 80px; /* Stop the navbar from covering the content */
    margin: 1% 3% 1% 3%;
}
/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 30px;
  height: 30px;
  top: 25px;
  right: 25px;
  display: none;
}

@media ( max-width: 768px ) {
  .bm-burger-button {
    display: block;
  }
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #545b64;
  transition: all .2s ease-in-out;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #288fe4;
  transition: all .2s ease-in-out;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: #292b2b;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;

  overflow: hidden !important; /* Override style on larger screens so out-of-place scroll bar isn't there -coal */
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
  margin-bottom: 11px;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}
a {
    text-decoration: inherit;
    color: inherit;
}

div.card-container {
    border-radius: 8px;
    border: 1px solid rgb(82, 82, 82);
    background-color: whitesmoke;

    height: auto;
    width: auto;
    max-width: 25vw;

    margin: 3vh;
}

@media (max-width: 620px) {
    div.card-container {
        max-width: 65vw;
    }
}

.card-content {
    margin: 2vh;
}
.text-center {
    text-align: center;
}
div.simple-flex-grid {
    display: flex;
    justify-content: center;
}

@media (max-width: 620px) {
    div.simple-flex-grid {
        display: grid;
        justify-content: center;
    }
}

.simple-flex-col {
    flex: 1 1;
}
p {
    color: #282d31;
}

.projectContainer {
    margin: 1% 17% 1% 17%;
}
